.skins__image {
  position: relative;
}
.skins {
  max-width: 1560px;
  margin: 80px auto;
}

.skins__img {
  width: 100%;
  object-fit: cover;
}

.skins__carousel {
  position: absolute;
  height: 100%;
  width: 31%;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
}

.corousel__title {
  position: relative;
  color: white;
  font-size: 40px;
  font-weight: 800;
  padding: 40px 20px;
  font-style: italic;
  letter-spacing: 0.05em;
  display: flex;
  justify-content: center;
}

.corousel__title::after {
  content: "";
  position: absolute;
  bottom: 35px;
  width: 80%;
  height: 1px;
  background: rgba(255, 255, 255, 0.2);
  transform-origin: left center;
}

.carousel {
  overflow: hidden;

  margin-left: 50px;
  height: 65vh;
}
.carousel__inner {
  display: flex;
  flex-direction: column;
  transition: 400ms;
  padding-top: 150px;
  scroll-snap-type: y mandatory;
  -webkit-overflow-scrolling: touch;
}

.carousel__item {
  display: flex;
  color: rgba(255, 255, 255, 0.6);
  padding: 20px;
  align-items: center;
}
.carousel__item span {
  margin: 20px;
  font-weight: 400;
  letter-spacing: 0.05em;
  font-weight: 600;
  font-size: 13px;
  text-transform: uppercase;
  transition: 300ms;
}

.carousel__img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  opacity: 0.7;
  transition: 300ms;
}

.carousel-active {
  outline: 1px solid rgba(255, 255, 255, 0.2);
  outline-offset: -7px;
}
.carousel-active img {
  transition: 300ms;
  transform: scale(1.7);
  opacity: 1;
}

.carousel-active span {
  transition: 300ms;
  margin-left: 40px;
  color: rgba(255, 255, 255, 0.9);
}
